import React, { forwardRef, useRef } from "react"

const Plane = forwardRef(({ color = "white", opacity = 1, args, map, ...props }, ref) => {
  const material = useRef()
  return (
    <mesh ref={ref} {...props}>
      <planeBufferGeometry args={args} />
      <meshBasicMaterial ref={material} color={color} map={map} transparent opacity={opacity} />
    </mesh>
  )
})

export default Plane
